import React, { useState, useContext } from 'react';
import { useLocalStorage, usePermission, useEffectOnce } from 'react-use';
import SceneVisible from 'context/SceneVisible';
import OverlayContext from 'context/OverlayContext/';
import Scene from 'components/Scene/';
import Button from 'components/Button/';
import Loader from 'react-loader-spinner';
import Audio from 'media/image/waiting-audio.svg';
import Video from 'media/image/waiting-video.svg';
import Location from 'media/image/waiting-location.svg';
import SharingDemo from 'media/image/enableSharing.png';
import { success2 } from 'media/audio/success/';
import playAudioInstance from 'api/playAudioInstance/';
import generateRandomNumber from 'api/generateRandomNumber/';
import './entry.css';

// const password = generateRandomNumber(6);
const participantID = generateRandomNumber(6);

function TableColumn({ title, children }) {
  return (
    <div className="tableColumn">
      <span className="textSemibold title">{title}:</span>
      <span className="children">{children}</span>
    </div>
  );
}

function WaitingTable() {
  const [id] = useLocalStorage('participantID', participantID);
  const [linkCopied, setLinkCopied] = useState(false);

  return (
    <div className="table">
      <TableColumn title="Meeting Topic">
        <span>The Remote Experience</span>
      </TableColumn>
      <TableColumn title="Host">
        <span className="textScrambled">Ricky Xie</span>
      </TableColumn>
      {/* <TableColumn title="Password">
        <span>{password}</span>
      </TableColumn> */}
      <TableColumn title="Invitation URL">
        <span
          className={(linkCopied) ? 'copied' : ''}
          onAnimationEnd={() => {
            setLinkCopied(false);
          }}
        >
          {window.location.href}
        </span>
        <button
          className="copyUrl"
          onClick={() => {
            navigator.clipboard.writeText(window.location.href).then(() => {
              setLinkCopied(true);
            });
          }}
        >
          <span className="textSmall">Copy URL</span>
        </button>
      </TableColumn>
      <TableColumn title="Participant ID">
        <span>{id}</span>
      </TableColumn>
    </div>
  );
}

function StatusItem({ permission, label, callback }) {
  const { setCameraStream } = useContext(OverlayContext);
  const sceneVisible = useContext(SceneVisible);

  const mic = usePermission({ name: 'microphone' });
  const cam = usePermission({ name: 'camera' });

  let p;
  let userMedia;
  let image;
  let successLabel;
  let successString;
  let caption;

  switch (permission) {
    case 'microphone':
      p = mic;
      userMedia = { audio: true };
      successLabel = 'Audio';
      successString = 'Audio Connected';
      image = (<img src={Audio} alt="" />);
      break;
    case 'camera':
      p = cam;
      userMedia = { video: true };
      successLabel = 'Video';
      successString = 'Video Connected';
      image = (<img src={Video} alt="" />);
      break;
    default:
      image = (<img src={Location} alt="" />);
  }

  if (permission) {
    caption = (p === 'granted') ? (
      <p className="textSmall textMedium caption success">{successString}</p>
    ) : (
      <p className="textSmall textMedium caption error">Not Connected</p>
    );
  } else {
    caption = (<p className="textSmall textMedium caption">Click to find out</p>)
  }

  const handleClick = () => {
    const errString = 'Permission denied!\nPlease enable microphone or camera access via your browser’s settings.';

    if (permission) {
      if (permission === 'camera') {
        navigator.mediaDevices.getUserMedia(userMedia)
        .then((stream) => {
          setCameraStream(stream);
        })
        .catch((err) => {
          setCameraStream(null);
          alert(errString);
        });
      } else {
        navigator.mediaDevices.getUserMedia(userMedia)
        .then((stream) => {})
        .catch((err) => alert(errString));
      }
    }

    if (callback) {
      callback();
    }
  };

  const childElements = (
    <>
      <div className="svgContainer">{image}</div>
      <p className="textSmall textSemibold">
        {(p === 'granted') ? successLabel : label}
      </p>
      {caption}
    </>
  );

  if (p === 'granted') {
    return (
      <div className="statusItem">{childElements}</div>
    )
  }

  return (
    <button
      className="statusItem"
      onClick={handleClick}
      disabled={(!sceneVisible)}
    >
      {childElements}
    </button>
  );
}

function Limbo() {
  useEffectOnce(() => {
    playAudioInstance(success2);
  });

  return (
    <div className="waitingRoom">
      <WaitingTable />
      <div className="statusContainer">
        <StatusItem permission="microphone" label="Join Audio" />
        <StatusItem permission="camera" label="Enable Video" />
        <OverlayContext.Consumer>
          {({ setUniqueState }) => (
            <StatusItem label="Where am I?" callback={() => setUniqueState('onboard')} />
          )}
        </OverlayContext.Consumer>
      </div>
    </div>
  );
}

function EntryButton({ callback = () => {} }) {
  const [triggered, setTriggered] = useState(false);

  return (triggered) ? (
    <div className="waitingForPrompt">
      <div className="waitingMessage">
        <Loader type="Oval" width="1.4em" height="1.4em" color="#3582E8" />
        <p className="textSemibold">
          Please wait, the meeting host will let you in soon.
        </p>
      </div>
      <img src={SharingDemo} width="350" height="220" alt="Screenshot of a browser prompt to enable access to the microphone and camera" />
      <p className="textSmall">This site uses your microphone and camera for a more immersive experience. When the browser prompts, click on <span className="textSemibold">Allow</span> to enable access.</p>
    </div>
  ) : (
    <>
      <Button
        blurOnInteract
        callback={() => {
          setTriggered(true)
          callback();
        }}
      >
        Join with Computer Audio
      </Button>
      <h1 className="entryText">Welcome to the Remote Experience</h1>
    </>
  );
}

function Entry({ callback = () => {} }) {
  return (
    <Scene name="entry">
      <OverlayContext.Consumer>
        {({ initiated }) =>
          (initiated) ? (
            <Limbo />
          ) : (
            <EntryButton callback={callback}  />
          )
        }
      </OverlayContext.Consumer>
    </Scene>
  );
}

export default Entry;
