import React, { useContext, useState, useEffect, useCallback } from 'react';
import { useKeyPressEvent, usePermission } from 'react-use';
import OverlayContext from 'context/OverlayContext/';
import Info from 'components/Overlay/Info';
import Button from 'components/Button/';
import { Microphone, Video, MicrophoneSlash, VideoSlash } from 'media/image/';
import { ReactComponent as Location } from 'media/image/location.svg';
import { ReactComponent as ArrowLeft } from 'media/image/arrow-left.svg';
import { ReactComponent as ArrowRight } from 'media/image/arrow-right.svg';
import playAudioInstance from 'api/playAudioInstance';
import { click3 } from 'media/audio/click/';
import expand from 'media/audio/expand/';
import collapse from 'media/audio/collapse';
import './overlay.css';
import './backgrounds/noise.css';
import './backgrounds/groovepaper.css';

const Top = React.memo(() => {
  const micPermission = usePermission({ name: 'microphone' });
  const videoPermission = usePermission({ name: 'camera' });

  const micPermissionGranted = (micPermission === 'granted');
  const videoPermissionGranted = (videoPermission === 'granted');

  let permissions = 'none';
  let permissionString = 'You are not sharing';

  if (micPermissionGranted && videoPermissionGranted) {
    permissions = 'both';
    permissionString = 'You are sharing and caring';
  } else if (
    (micPermissionGranted && !videoPermissionGranted) ||
    (!micPermissionGranted && videoPermissionGranted)) {
    permissions = 'single';
    if (!micPermissionGranted) {
      permissionString = 'Please share audio';
    }
    if (!videoPermissionGranted) {
      permissionString = 'Please share video';
    }
  }

  return (
    <div className="topContainer">
      <div className={`topThing ${permissions}Permission`}>
        <div className="svgContainer">
          {(micPermission === 'granted') ? (
            <Microphone fill="#000" fillOpacity="0.65" />
          ) : (
            <MicrophoneSlash
              fill={(permissions === 'single') ? '#000' : '#fff'}
              fillOpacity={(permissions === 'single') ? '0.65' : '1'}
            />
          )}
        </div>
        <span>{permissionString}</span>
        <div className="svgContainer">
          {(videoPermission === 'granted') ? (
            <Video fill="#000" fillOpacity="0.65" />
          ) : (
            <VideoSlash
              fill={(permissions === 'single') ? '#000' : '#fff'}
              fillOpacity={(permissions === 'single') ? '0.65' : '1'}
            />
          )}
        </div>
      </div>
    </div>
  );
});

function ProgressBar({ index, lastIndex }) {
  return (
    <div className="progressContainer">
      <div
        className="progress"
        style={{ transform: `scaleX(${index / (lastIndex - 1)})` }}
      />
    </div>
  );
}

const BarButton = React.forwardRef(({ children, name, disabled = false, callback, title }, ref) => (
  <button
    className="barButton"
    name={name}
    onMouseDown={() => playAudioInstance(click3)}
    onClick={callback}
    disabled={disabled}
    title={title}
    ref={ref}
  >
    {children}
  </button>
));

function Overlay({ visible = true, callbacks = {} }) {
  const { index, lastIndex, uniqueState, setUniqueState } = useContext(OverlayContext);
  const [modalVisible, setModalVisible] = useState(false);
  const [infoVisible, setInfoVisible] = useState(false);

  const toggleInfo = useCallback((state) => {
    const show = () => {
      if (!infoVisible) {
        setInfoVisible(true);
        callbacks.info();
        playAudioInstance(expand);
      }
    };

    const hide = () => {
      if (infoVisible) {
        setInfoVisible(false);
        callbacks.dismissInfo();
        playAudioInstance(collapse);
        if (uniqueState === 'onboard') setUniqueState(null);
      }
    };

    switch (state) {
      case 'show':
        show();
        break;
      case 'hide':
        hide();
        break;
      default:
        if (infoVisible) {
          hide();
        } else if (!infoVisible && document.activeElement.tagName !== ('INPUT')) {
          show();
        }
    }
  }, [callbacks, infoVisible, setUniqueState, uniqueState]);

  const toggleModal = useCallback((type, boolean = !modalVisible) => {
    const action = (boolean) ? 'show' : 'hide';

    setModalVisible(boolean);

    switch (type) {
      case 'info':
        toggleInfo(action);
        break;
      case 'all':
        toggleInfo(action);
        break;
      default:
        toggleInfo(action);
    }
  }, [toggleInfo, modalVisible]);

  const handleLeave = () => {
    if (uniqueState === 'breakout') {
      setUniqueState(null);
    } else {
      setModalVisible(true);
      callbacks.leave();
      setTimeout(() => {
        setModalVisible(false);
      }, 1000);
    }
  };

  useKeyPressEvent('Escape', () => toggleModal('all', false));
  useKeyPressEvent('i', () => {
    if (document.activeElement.tagName !== 'INPUT') toggleModal('info');
  });

  useEffect(() => {
    if (uniqueState === 'onboard') {
      toggleModal('info', true);
    }
  }, [toggleInfo, toggleModal, uniqueState])

  return (
    <div className={(visible) ? 'overlay visible' : 'overlay'}>
      <Top />

      <Info
        visible={infoVisible}
        onDismiss={() => toggleModal('info', false)}
      />

      <nav className={`bar${(uniqueState === 'inaccessibility') ? ' inaccessible' : ''}`}>
        {/* progress */}
        <ProgressBar index={index} lastIndex={lastIndex} />

        {/* prev/next */}
        <div className="buttonGroup">
          {/* prev */}
          {(index > 0) && (
            <BarButton name="previous"
              callback={() => {
                setUniqueState(null);
                callbacks.prev();
              }}
              disabled={(index <= 1 || infoVisible)}
              title="Return to the previous scene"
            >
              <ArrowLeft />
              Prev
            </BarButton>
          )}
          {/* next */}
          <BarButton name="next"
            callback={() => {
              setUniqueState(null);
              callbacks.next();
            }}
            disabled={(index === lastIndex - 1 || infoVisible)}
            title="Go to the next scene"
          >
            <ArrowRight />
            Next
          </BarButton>
        </div>
        {/* info toggle */}
        <BarButton
          name="info"
          callback={() => toggleModal('info', true)}
          disabled={(infoVisible)}
          title="Click or press the “i” key to open"
        >
          <Location />
          Where am I?
        </BarButton>
        {/* leave */}
        {(index > 0) && (
          <Button
            className="leaveButton"
            callback={handleLeave}
            disabled={(infoVisible)}
          >
            {(uniqueState === 'breakout') ? 'Leave Breakout Room' : 'Leave'}
          </Button>
        )}
      </nav>

      {/* vignette */}
      <div
        className={(modalVisible) ? 'vignette noise dim' : 'vignette noise'}
        onClick={() => toggleModal('all', false)}
      />
    </div>
  );
}

export default Overlay;
